.container {
margin: 50px 0;
}

.card {
margin: 0 2% !important;
}

.infected {
border-bottom: 10px solid rgba(255,0,255,.6);
}
.recovered {
border-bottom: 10px solid rgba(0,255,0,.6);
}
.deaths {
border-bottom: 10px solid rgba(255,0,0,.6);
}

@media (max-width: 770px) {
    .card{
        margin: 2% 0 !important;
    }
}